html {
    height: 100%;
    font-size: 62.5%;
    scroll-behavior: smooth;
}


body {
    min-height: 100%;
    font-family: $primaryFont;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    position: relative;
}

.wrapper {
    margin: 0 auto;
    max-width: 1280px;
    width: 90%;
}

// LINK Cleanup
a, main li{
    text-decoration: none;
    list-style: none;
    color: black;
}

// FONTS
// Landing Page
h1, .hContainer p { 
    color: $secondaryColor;
} 
h1 {
    font-family: $primaryFont;
    font-size: calc(2.3vw + 2.7rem);
    text-transform: uppercase;
    margin-bottom:0;
}
.hContainer p {
    font-family: $secondaryFont;
    font-size: calc((2.6vw + 2.4rem) * .6);
    text-transform: lowercase;
        &::first-letter{
            text-transform: uppercase;
        }
}

// MAIN Page
main h1{
    color: $secondaryColor;
    margin: 0;
    padding:3% 5% 1% 5%;
}
h2{
    font-size: calc(.1vw + 1.7rem);
    color: $primaryColor;
    padding: 4% 0 0 0;
    margin: 0 auto;
    text-align: center;  
}
h3{
    font-family: $secondaryFont;
    font-size: calc(.2vw + 1.6rem);
    text-transform: capitalize;
    margin: 2.5% 0 .5% 0;
}
main p{
    font-family: $secondaryFont;
    font-size: calc(.2vw + 1.6rem);
    text-align: left;
    margin: .05%;
    padding: 0;
}

.landingButton:focus,
.landingButton:hover{
    letter-spacing: 2px;
    padding:10px 25px 8px 25px;
    transition: all 0.5s; 
    border-radius: 0;  
    background-color: rgba(0,0,0,0.8);
    color:white;    
}


button,
form{
    background-color: $secondaryColor;
    border: none;
    font-family: $secondaryFont;
    font-size: calc((.2vw + 1.6rem));
    text-transform: uppercase;
}

button{
    padding: 10px 19px 8px 19px;
    border-radius: 2px;
    cursor: pointer;
}


// Search-Main Page
form {
    display: flex;
}
main input {
    color: $primaryColor;
    text-align: center;
    padding: 7px;
}
.mButton{
    background-color: $primaryColor;
    color: $secondaryColor;
    font-size: calc((.1vw + 1.6rem));
    
}

.mButton:hover,
.mButton:focus{
    background-color: black;
}

main button {
    font-size: calc((.1vw + 1.6rem));
    text-transform: capitalize;
    text-align: left;
}

// GRIDS ...Lists of Books
.movies,
.books{
    grid-column: 2/12;
}

.buttonChoices {
    border: 1px  #81003c58 solid;
    margin:3px;
    border-radius: 3px;
    display: inline;
}
.buttonChoices:hover,
.buttonChoices:focus{
    -webkit-box-shadow: 2px 4px 6px 2px rgba(18,18,18,0.26);
    -moz-box-shadow: 2px 4px 6px 2px rgba(18,18,18,0.26);
    box-shadow: 2px 4px 6px 2px rgba(18,18,18,0.26);
    letter-spacing: 1px;
    transition: all 0.9s;
}

.moreDetail { 
    width: 280px;
    text-align: center;
    margin:-4% 0 11% 0;
}

.rating{
    font-weight: bold;
}


@keyframes flicker {
    0%{
        color:transparent;
    }
    20% {
        color:#ff7600;
        -webkit-text-stroke: .5px black;
    }   
    40% {
        color: transparent
    }
    60% {
        color:#ff7600;
        -webkit-text-stroke: .5px black;
    }
    80% {
        color:#ff7600;
        -webkit-text-stroke: .5px black;
    }
    100% {
        color: transparent;
    }
}

.selectedImg{
    position: relative;
}

.winner{
    position: relative;
    top: -300px;
    transform: rotate(-20deg);
    font-size: 4.2rem;
    font-weight: bold;
    animation: flicker 1s cubic-bezier(0.19, 1, 0.22, 1) infinite;
    font-family: 'Russo One', sans-serif;
}

footer{
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    width: 100%;
    opacity: .5;
    flex-wrap: wrap;
}

footer p {
    margin: 10px;
}


